import React from "react";
import { Card } from '@material-ui/core';
import { Button } from '@material-ui/core';
import "../App.css";
export default class favPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: []
        };
    }
    componentDidMount() {
        let moviesList = JSON.parse(localStorage.getItem("movies"));
        console.log("movies list", moviesList);
        this.setState({
            movies: moviesList,
        });
    }
    handleBack = (e) => {
        try {
            e.preventDefault();
            this.props.history.push('/');
        } catch (err) {
            console.log("error at handleBack");
        }
    };
    render() {
        console.log("print", this.state.movies);
        return (
            <div className="dashboard">
                <div style={{ marginRight: "96%" }}>
                    <Button variant="contained" size="large" onClick={this.handleBack}>Back</Button>
                </div>
                <div className="card1">
                    {
                        !this.state.movies ? (
                            <div style={{ display: "block", marginLeft: "40%" }}>
                                <img style={{ width: "200%" }} src={require("../images/unavail.png")}
                                    alt="" />
                            </div>)
                            : (
                                this.state.movies.map(
                                    (movie, index) =>
                                        <div key={index} style={{}}>
                                            {movie.isFavourite === true ?
                                                <Card className="createNote">
                                                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                                                        <div>
                                                            <div>
                                                                <img src={movie.Poster} alt="poster"
                                                                    style={{ width: "30%" }}>
                                                                </img>
                                                            </div>
                                                            <div><h2>{movie.Title}</h2>
                                                                {movie.Year}</div>
                                                        </div>
                                                    </div>
                                                </Card> : null}
                                        </div>
                                ))
                    }
                </div>
            </div>
        );
    }
}
